import {formatTime} from "../../../helpers/formatTime";
import QuickDelete from "./formatting/QuickDelete";
import TablePage from "./TablePage";

const Tokens = () => {
  const filters = [
    { label: '', value: {} }
  ]

  const columns = [
    { label: '', element: (row) => row.name },
    { label: '', element: (row) => formatTime(row.exp) },
    { label: '', element: (row) => <QuickDelete row={row}/> }
  ]

  return (
    <TablePage
      title="API "
      filters={filters}
      columns={columns}
    />
  );
};

export default Tokens;