import {useTheme} from "@mui/material";
import SecondaryWindow from "./SecondaryWindow";

const SecondaryWindows = ({ children }) => {
  const theme = useTheme();

  return (
    <div className='secondary-windows'>
      <SecondaryWindow>
        <span style={{color: theme.palette.primary.main}}>     </span>,
             ,   
      </SecondaryWindow>
      <SecondaryWindow>
                <span
        style={{color: theme.palette.primary.main}}>    ,    </span>
      </SecondaryWindow>
      <SecondaryWindow>
            <span style={{color: theme.palette.primary.main}}>  </span>, 
             .
      </SecondaryWindow>
    </div>
  )
}

export default SecondaryWindows